import { ChainId } from 'sdk'
import getTokens from 'sdk/tokens'
import getSyruppools from 'sdk/syruppools'
import getLiquiditypools from 'sdk/liquiditypools'

export const getGrausAddress = () => {
  return getTokens(ChainId.HTNET).graus.address
}

export const getGrausDecimals = () =>
{
  return getTokens(ChainId.HTNET).graus.decimals
}

export const getSyrupPoolContractAddress = (id: number) => {
  const config = getSyruppools().find((pool) => pool.syrupId === id)

  return  config.contractAddress
}

export const getLiquidityPoolContractAddress = (id: number) => {
  const config = getLiquiditypools().find((pool) => pool.liquidityid === id)

  return  config.contractAddress 
}
 