import htLiquiditypools from './128'
import bscLiquiditypools from './56'
import { ChainId, ENVChainId } from '../constants'

const liquiditypools = {
  [ChainId.BSCNET]: bscLiquiditypools,

  [ChainId.HTNET]: htLiquiditypools
}

export default function getLiquiditypools(chainId? : ChainId)
{
   return chainId ? liquiditypools[chainId] : liquiditypools[ENVChainId]
}